#intro {
  background: linear-gradient(180deg, rgba(25,25,25,0) 0%, rgba(25,25,25,1) 76%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 8rem;
  position: relative;
}
  
#intro::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url('../assets/images/clouds.webp');
  background-position: center;
  background-size: cover;
  opacity: .2;
  z-index: -1;
  background-attachment: fixed;
}